<template>
  <el-dialog title="" :visible.sync="dialogVisible" :close-on-click-modal="false" width="100%" @close='resetDialog'>
  </el-dialog>
</template>
<script>
import { apiUpdatePatientCureForm, apiCreatePatientCureForm } from '@/api/api'
export default {
  name:'CureForm',
  data() {
    return {
      cureForm:{},
      dialogVisible: false,
      exist: true,
    }
  },
  methods: {
    updateCureForm () {
      apiUpdatePatientCureForm(this.cureForm)
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    createCureForm () {
      apiCreatePatientCureForm(this.cureForm)
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    resetDialog () {
      this.cureForm = {}
    }
  },
  created() {
    this.$on("openEvent", (data)=>{
      if (!data.exist) {
        //
        this.cureForm.info = data.cureForm.info
        this.dialogVisible = true
        this.exist = data.exist
      } else {
        //()
        this.cureForm = data.cureForm
        this.dialogVisible = true
        this.exist = data.exist
      }

    })
  }

};
</script>
<style lang="">
</style>


